<script land="script" scoped>
import headerBar from '@/components/header/headerBar.vue'
import Backtop from '@/components/backtop/backtop' // 返回顶部
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'JjJbrHall',
  components: {
    Backtop,
    headerBar
  },
  // mixins: [pageRouter],
  data() {
    return {
      btnScrollTop: 0, // 导航条距离顶部的距离
      headerData: {
        title: '竞价详情',
        leftUrl: 'back',
        leftText: ' '
      }
    }
  },
  mounted() {
    const btnScrollTop = document.querySelector('.operation-btn').offsetTop
    this.btnScrollTop = btnScrollTop // 挂载后获取到导航条距离顶部的距离，然后给data.btnScrollTop

    // 监听滚动条滚动的距离，超过导航条距离顶部的距离则添加相应类名，小于这删除相应类名
    document.addEventListener('scroll', () => {
      const target = document.documentElement.scrollTop
      if (target >= this.btnScrollTop) {
        this.flag = true
      } else {
        this.flag = false
      }
    })
  },
  methods: {
    goSuplist() {
      this.$router.push({
        name: 'JjSupList'
      })
    },
    SendXx() {
      this.$toast.success('消息发送成功')
    }
  }
}
